<script>
import axios from "axios";

export default {
  name: "app",
  data() {
    return { versao: '' }
  },
  methods: {
    routeTo(route) {
      this.$router.push(route);
    },
    onLoad() {
      axios({
        method: "get",
        url: "/api/versao"
      })
        .then(response => {
           this.versao = response.data.versao;          
        })
        .catch(e => {
          // eslint-disable-next-line
          console.error(e);
        });
    }
  },
  created() {
    this.onLoad();
  }
};
</script>

