<template>
  <div>
    <b-container>
      <div>
        <b-alert
          dismissible
          variant="danger"
          :show="showErro"
          @dismissed="showErro=false"
        >{{ erroMsg }}</b-alert>
        <b-alert
          dismissible
          variant="success"
          :show="dismissCountDown"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged"
        >{{ okMsg }}</b-alert>
      </div>
    </b-container>
    <b-container style="padding: 10px">
      <b-row>
        <b-col>
          <b-form class @submit="onSubmit" @reset="onReset">
            <b-card no-body>
              <b-tabs card>
                <b-tab title="Configurações das Bandeiras de Cartões" active>
                  <b-row>
                    <b-col>
                      <b-form-group class="text-left" label="Bandeira:" label-for="bandeira">
                        <b-form-input
                          id="bandeira"
                          type="text"
                          v-model="bandeira"
                          required
                          placeholder="Exemplo: VISA"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                    <b-col>
                      <b-form-group class="text-left" label="Código Débito:" label-for="debito">
                        <b-form-input
                          id="debito"
                          type="text"
                          v-model="debito"
                          placeholder="Exemplo: 20002"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                    <b-col>
                      <b-form-group class="text-left" label="Código Crédito:" label-for="credito">
                        <b-form-input
                          id="credito"
                          type="text"
                          v-model="credito"
                          placeholder="Exemplo: 1"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col v-b-tooltip.hover.topleft title="Marque essa opção caso o código seja de um cartão vale/voucher">
                      <b-form-checkbox id="methodCard" v-model="vale">Cartão tipo Vale/Voucher</b-form-checkbox>                    
                    </b-col>
                    <b-col>
                    </b-col>
                    <b-col>
                    </b-col>                   
                  </b-row>
                </b-tab>
              </b-tabs>
            </b-card>
            <div class="mt-2 text-right">
              <b-button type="submit" variant="primary">Salvar</b-button>
              <b-button class="ml-2" type="reset" variant="danger">Limpar</b-button>
            </div>
          </b-form>
        </b-col>
      </b-row>
      <hr />
    </b-container>
    <b-container>
      <b-table striped hover small :items="cartoes" busy="isBusy" :fields="fields">
        <template slot="actions" slot-scope="row">
          <b-button size="sm" @click="edit(row.item)" variant="primary" style="margin-right: 2px;">
            <font-awesome-icon icon="edit" />
          </b-button>
          <b-button size="sm" @click="showConfirmacao(row.item.index)" variant="danger">
            <font-awesome-icon icon="trash-alt" />
          </b-button>
        </template>
      </b-table>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      bandeira: "",
      debito: "",
      credito: "",
      vale: false,
      index: -1,
      cartoes: [],
      fields: [
        {
          key: "bandeira",
          label: "Bandeira",
          class: "text-center"
        },
        {
          key: "debito",
          label: "Cód. Débito",
          class: "text-right"
        },
        {
          key: "credito",
          label: "Cód. Crédito",
          class: "text-right"
        },
        {
          key: "tipoModal",
          label: "Vale/Voucher",
          class: "text-center",
          formatter: (value) => {
            /* eslint-disable */
            return value == 3 ? "Sim" : "Não";
          }
        },
        { key: "actions", label: "Ações" }
      ],
      isBusy: false,
      showErro: false,
      dismissSecs: 5,
      dismissCountDown: 0,
      erroMsg: "",
      okMsg: "",
    };
  },
  methods: {
    showOK(msg) {
      this.okMsg = msg;
      this.dismissCountDown = this.dismissSecs;
    },
    showFail(msg) {
      this.erroMsg = msg;
      this.showErro = true;
    },
    edit(item) {
      console.log(item);
      this.bandeira = item.bandeira;
      this.debito = item.debito;
      this.credito = item.credito;
      this.vale = item.tipoModal == 3;
      this.index = item.index;      
    },
    showConfirmacao(index) {
      this.$bvModal
        .msgBoxConfirm("Deseja mesmo deletar a configuração?", {
          title: "Por favor confirme!",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "SIM",
          cancelTitle: "NÃO",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        })
        .then(value => {
          if (value) {
            axios({
              method: "post",
              url: "/api/cfg-cartoes/delete",          
              data: { index: index }
            })
              .then(response => {
                this.onLoad();
                if (response.status == 200) {
                  this.showOK("Cadastro deletado!");
                  this.reset();
                } else {
                  this.showFail("Houve problemas ao Deletar o cadastro!");
                }
              })
              .catch(e => {
                // eslint-disable-next-line
                console.log(e);
              });
          }
        })
        .catch(err => {
          // An error occurred
          // eslint-disable-next-line
          console.log(err);
        });
    },
    validate() {
      let res = false;
      if (!this.bandeira) {
        this.showFail("Favor informar a Bandeira do Cartão");
      } else if ((!this.debito) && (!this.credito)) {
        this.showFail("Favor informar a pelo menos um Código de Débito ou Crédito");  
      } else if ((this.debito) && (this.credito) && (this.vale)) {
        this.showFail("Quando o cartão for do tipo Vale/Voucher favor informar somente um código de Crédito ou Débito!");               
      } else {
        let jaExiste = false;
        var that = this;
        this.cartoes.some(el => {
          jaExiste =
            that.bandeira == el.bandeira &&          
            that.index != el.index;
          if (jaExiste) return jaExiste;
        });
        if (jaExiste) {
          this.showFail("Configuração já existe!");
        } else {
          res = true;
          this.showErro = false;
        }
      }
      return res;
    },
    onSubmit(evt) {
      evt.preventDefault();
      if (this.validate()) {
        let obj = {
          index: this.index,
          bandeira: this.bandeira,
          debito: this.debito,
          credito: this.credito,
          vale: this.vale
        };
        axios({
          method: "post",
          url: "/api/cfg-cartoes",
          data: obj
        })
          .then(response => {
            this.onLoad();
            if (response.status == 200) {
              this.showOK("Configurações atualizadas!");
              this.onReset();
            } else {
              this.showFail("Houve problemas ao salvar as configurações!");
            }
          })
          .catch(e => {
            // eslint-disable-next-line
            console.log(e);
          });
      }
    },
    onReset(evt) {
      this.bandeira = "";
      this.debito = "";
      this.credito = "";
      this.vale = false;
      this.index = -1;      
    },
    onLoad() {
      axios({
        method: "get",
        url: "/api/cfg-cartoes"
      })
        .then(response => {
          this.cartoes = response.data.cartoes;
          this.cartoes = this.cartoes.map((el,index) => { 
           el.index = index;
           return el;
           });
        })
        .catch(e => {
          // eslint-disable-next-line
          console.error(e);
        });
    }
  },
  created() {
    this.onLoad();
  }
};
</script>