<template>
  <div>
    <b-container>
      <div>
        <b-alert
          dismissible
          variant="danger"
          :show="showErro"
          @dismissed="showErro=false"
        >{{ erroMsg }}</b-alert>
        <b-alert
          dismissible
          variant="success"
          :show="dismissCountDown"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged"
        >{{ okMsg }}</b-alert>
      </div>
    </b-container>
    <b-container>
      <b-row class="justify-content-md-center">
        <b-col>
          <b-form class @submit="onSubmit" @reset="onReset">
            <b-card no-body>
              <b-tabs card>
                <b-tab title="Configurações do Servidor" active>
                  <b-row>
                    <b-col>
                      <b-form-group
                        id="InputGroupUser"
                        class="text-left"
                        label="Usuário:"
                        label-for="InputUser"
                      >
                        <b-form-input
                          id="InputUser"
                          type="text"
                          v-model="cfg.user"
                          required
                          placeholder="Admin"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                    <b-col>
                      <b-form-group
                        id="InputGroupPass"
                        class="text-left"
                        label="Senha:"
                        label-for="InputPass"
                      >
                        <b-form-input
                          id="InputPass"
                          type="password"
                          v-model="cfg.password"
                          required
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col>
                      <b-form-group
                        id="InputGroupServ"
                        class="text-left"
                        label="Endereço Servidor:"
                        label-for="InputServ"
                      >
                        <b-form-input
                          id="InputServ"
                          type="text"
                          v-model="cfg.servidor"
                          required
                          placeholder="localhost"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                    <b-col>
                      <b-form-group
                        id="InputGroupInst"
                        class="text-left"
                        label="Instância:"
                        label-for="InputInst"
                      >
                        <b-form-input
                          id="InputInst"
                          type="text"
                          v-model="cfg.instancia"
                          required
                          placeholder="Default"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col>
                      <b-form-group
                        id="InputGroupDb"
                        class="text-left"
                        label="Banco de Dados:"
                        label-for="InputDb"
                      >
                        <b-form-input
                          id="InputDb"
                          type="text"
                          v-model="cfg.base"
                          required
                          placeholder="ATXDADOS"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                    <b-col>
                      <b-form-group
                        id="InputGroupPort"
                        class="text-left"
                        label="Porta XPPagamentos:"
                        label-for="InputPort"
                      >
                        <b-form-input
                          id="InputPort"
                          type="number"
                          v-model="cfg.porta"
                          required
                          placeholder="3333"
                        ></b-form-input>
                      </b-form-group>
                    </b-col>
                  </b-row>                  
                </b-tab>
                <b-tab title="Configurações Operacionais">
                  <b-row>
                    <b-col>
                      <b-form-checkbox v-model="cfg.log">Gerar log dos eventos</b-form-checkbox>
                    </b-col>                   
                  </b-row>                  
                </b-tab>
              </b-tabs>
            </b-card>
            <div class="mt-2 text-right">
              <b-button type="submit" variant="primary">Salvar</b-button>
              <!--<b-button class="ml-2" type="reset" variant="danger">Limpar</b-button>-->
            </div>
          </b-form>
        </b-col>
      </b-row>
      <hr />
    </b-container>    
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      showErro: false,
      dismissSecs: 5,
      dismissCountDown: 0,
      erroMsg: "",
      okMsg: "",     
      cfg: {
        log: false,       
        user: "",
        password: "",
        servidor: "",
        database: "",
        grupoProdutos: [],
        instancia: "",
        porta: ""
      }
    };
  }, 
  methods: {    
    showOK(msg) {
      this.okMsg = msg;
      this.dismissCountDown = this.dismissSecs;
    },
    showFail(msg) {
      this.erroMsg = msg;
      this.showErro = true;
    },   
    onLoad() {
      axios({
        method: "get",
        url: "/api/cfg"
      })
        .then(response => {
          this.cfg.log = response.data.log;         
          this.cfg.user = response.data.user;
          this.cfg.password = response.data.password;
          this.cfg.servidor = response.data.servidor;
          this.cfg.database = response.data.database;        
          this.cfg.porta = response.data.porta;
          if (response.data.servidor.indexOf("\\") > 0) {
            this.cfg.servidor = response.data.servidor.substr(
              0,
              response.data.servidor.indexOf("\\")
            );
            this.cfg.instancia = response.data.servidor.substr(
              response.data.servidor.indexOf("\\") + 1
            );
          } else {
            this.cfg.servidor = response.data.servidor;
            this.cfg.instancia = "DEFAULT";
          }
          this.cfg.base = response.data.database;         
        })
        .catch(e => {
          // eslint-disable-next-line
          console.error(e);
        });
    },
    onSubmit(evt) {
      evt.preventDefault();
      const obj = this.cfg;
      axios({
        method: "post",
        url: "/api/cfg",
        data: obj
      })
        .then(response => {
          if (response.status == 200) {
            this.showOK("Configurações atualizadas!");
          } else {
            this.erroMsg = "Houve problemas ao salvar as configurações!";
            this.showErro = true;
          }
        })
        .catch(e => {
          // eslint-disable-next-line
          console.log(e);
          this.$eventHub.$emit("saveCfg", 404);
        });
    },
    onReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.cfg.log = false;    
      this.cfg.user = "";
      this.cfg.password = "";
      this.cfg.servidor = "";
      this.cfg.database = "";
      this.cfg.instancia = "";     
    }
  },
  created() {
    this.onLoad();
  }
};
</script>

